import {useState} from 'react'

export const useCounter = (numeroInicio : number) => {
    
    const [state, setstate] = useState( parseInt( localStorage.getItem('lastNumber') ?? numeroInicio.toString() ) );

    const sumando = (numero: number) => {
        setstate(state + numero);
    }

    localStorage.setItem('lastNumber', state.toString() )

    return { state, sumando };
}
